import React from 'react'
import { Card as CardComponent } from 'theme-ui'
import { Layout, Stack, Main } from '@layout'
import CardList from '@components/CardList'
import Divider from '@components/Divider'
import Seo from '@widgets/Seo'
import AuthorExpanded from '@widgets/AuthorExpanded'
import {
  PostHead,
  // PostImage,
  PostImage2,
  PostBody,
  PostComments,
  PostCommentsFacebook,
  PostCommentsGraph,
  PostTagsShare
} from '@widgets/Post'

// Michael Lu, 2024/05/10
import CookieAlert from '@qiyo_components/cookiealert/CookieAlert'
import UsageAlert from '@qiyo_components/usagealert/UsageAlert'

// Michael Lu, 2024/05/08
const Post = ({
  data: { post, tagCategoryPosts, tagPosts, categoryPosts, previous, next },
  ...props
}) => {
  const relatedPosts = [
    ...(tagCategoryPosts ? tagCategoryPosts.nodes : []),
    ...(tagPosts ? tagPosts.nodes : []),
    ...(categoryPosts ? categoryPosts.nodes : [])
  ]
  const { pageContext: { services = {}, siteUrl } = {} } = props

  // console.log('Post: props=', props)
  // console.log('Post: props.location=', props.location)
  // console.log('Post: post=', post)
  // console.log('Post: TEST')

  return (
    <>
      <Layout {...props}>
        <Seo {...post} siteUrl={siteUrl} />
        <Divider space={3} />
        <Stack>
          <Main>
            <CardComponent variant='paper-lg'>
              <PostImage2 {...post} inCardLarge />
              <PostHead {...post} />
              {false && <Divider line />}
              <Divider space={2} />
              <PostBody {...post} location={props.location} />
              <PostTagsShare {...post} location={props.location} />
              {services.disqus && <PostComments {...post} />}
              {services.graphComment && <PostCommentsGraph {...post} />}
              {services.facebookComment && (
                <PostCommentsFacebook {...post} siteUrl={siteUrl} />
              )}
            </CardComponent>
            {false && !post.hideAuthor && (
              <>
                <Divider />
                <AuthorExpanded author={post.author} />
              </>
            )}

            {!post.hideTags && post.category && (
              <>
                <Divider />
                <CardList
                  title='相關'
                  nodes={relatedPosts}
                  variant={['horizontal-md']}
                  columns={[1, 2, 2, 2]}
                  limit={6}
                  distinct
                />
              </>
            )}
          </Main>
        </Stack>
      </Layout>

      <CookieAlert></CookieAlert>
      <UsageAlert></UsageAlert>
    </>
  )
}

export default Post
